.App {
  text-align: center;
  margin: 0;
}



.App-header {
   min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
 
}

h1{
  font-family: 'stoic';
  font-size: 80px;
  text-shadow: black 3px 5px 2px;
}

p{
  margin-top: -60px;
  margin-left: 60px;
  font-size: 15px;
  
}

.background{
   
 margin-top: 90px;
 
}
@media (max-width:800px ) {
  
  .background{
   
    margin-top: 99px;
   
    width:99% ;
  
   }
 
  }


@media (max-width:600px ) {
  
  .background{
   
    margin-top: 90px;
   
    width:90% ;
    
    height: 450px;
   }
 
  }


 @media (max-width:400px ) {
  
  
  .background{
   
    margin-top: 20px;
   
    width:90% ;
    
    height: 450px;
   }
   h1{
    font-family: 'stoic';
    font-size: 60px;
    text-shadow: black 3px 5px 2px;
  }
  
  p{
    margin-top: -50px;
    margin-left: 60px;
    font-size: 12px;
    
  }
  }

